import React from 'react'
import { Grid, Box, Paper, Card, CardMedia, Typography } from '@material-ui/core'

import myPhoto from '../../assets/my-photo.jpg'
import { makeStyles } from '@material-ui/styles'
import Rating from '@material-ui/lab/Rating';
import GitIcon from '@material-ui/icons/GitHub'
import Instagram from '@material-ui/icons/Instagram'
import LinkedIn from '@material-ui/icons/LinkedIn'

const styles = makeStyles(theme => ({
    large: {
        width: "50%",
        height: "50%"
    },
    paper: {
        [theme.breakpoints.up('sm')]: {
            height: "100%"
        }
    },
    grid: {
        width: "100%"
    },
    media: {
        [theme.breakpoints.up('sm')]: {
            height: 395,
            width: 296
        }
    },
    boxShadow: {
        boxShadow: `-webkit-box-shadow: 10px 10px 5px -5px rgba(0,0,0,0.75);
        -moz-box-shadow: 10px 10px 5px -5px rgba(0,0,0,0.75);
        box-shadow: 10px 10px 5px -5px rgba(0,0,0,0.75);        
        `
    }
}))

export default function index() {
    const classes = styles()

    return (
        <>
            <Grid container spacing={2}>
                {/* my photo */}
                <Grid item xs={12} sm={6} >
                    <Box marginTop={3} marginX={1} height={1} display="flex" alignItems="center" justifyContent="center">
                        <Box boxShadow={5} padding={1}>
                            <Card>
                                <CardMedia
                                    component="img"
                                    alt="Alfredo Tito"
                                    className={classes.media}
                                    image={myPhoto}
                                    title="Alfredo Tito"
                                />
                            </Card>
                        </Box>
                    </Box>
                </Grid>
                {/* about me */}
                <Grid item xs={12} sm={6} >
                    <Box marginTop={3} marginX={1} display="flex" flexDirection="column" justifyContent="space-around" alignItems="center" >
                        <Grid container spacing={4}>
                            <Grid item>
                                <Paper elevation={3} className={classes.boxShadow}>
                                    <Box padding={1}>
                                        <Typography variant="body1">
                                            I'm Alfredo, a front-end, UX and UI enthusiast. I live in São Luís, Maranhão, Brazil. I am a researcher at UFMA's telemedia laboratory, where I developed the Fábulas project to create interactive stories. I do an internship at UNASUS as a developer of educational resources.
                                            <br/>
                                            Social Media:
                                            <br/>
                                            <Box width={0.25} display="flex" justifyContent="space-between">
                                                <a href="https://github.com/alfredots">
                                                    <GitIcon/>
                                                </a>
                                                <a href="https://www.instagram.com/garotocaos_/">
                                                    <Instagram/>
                                                </a> 
                                                <a href="https://www.linkedin.com/in/alfredo-tito-837429ba/">
                                                    <LinkedIn/>
                                                </a> 
                                            </Box> 
                                        </Typography>
                                    </Box>
                                </Paper>
                            </Grid>
                            <Grid item className={classes.grid}>
                                <Paper elevation={3} className={classes.boxShadow}>
                                    <Grid container>
                                        <Grid xs={6}>
                                                <Box padding={1}>
                                                    <Box component="fieldset" mb={3} borderColor="transparent">
                                                        <Typography component="legend">W.Development</Typography>
                                                        <Rating name="read-only" value={3} readOnly />
                                                    </Box>
                                                    <Box component="fieldset" mb={3} borderColor="transparent">
                                                        <Typography component="legend">Javascript</Typography>
                                                        <Rating name="read-only" value={3} readOnly />
                                                    </Box>
                                                    <Box component="fieldset" mb={3} borderColor="transparent">
                                                        <Typography component="legend">CSS</Typography>
                                                        <Rating name="read-only" value={2} readOnly />
                                                    </Box>
                                                </Box>
                                        </Grid>
                                        <Grid xs={6}>
                                            
                                                <Box padding={1}>
                                                    <Box component="fieldset" mb={3} borderColor="transparent">
                                                        <Typography component="legend">Stylus</Typography>
                                                        <Rating name="read-only" value={1} readOnly />
                                                    </Box>
                                                    <Box component="fieldset" mb={3} borderColor="transparent">
                                                        <Typography component="legend">React</Typography>
                                                        <Rating name="read-only" value={3} readOnly />
                                                    </Box>
                                                    <Box component="fieldset" mb={3} borderColor="transparent">
                                                        <Typography component="legend">MongoDB</Typography>
                                                        <Rating name="read-only" value={1} readOnly />
                                                    </Box>
                                                </Box>
                                            
                                        </Grid>
                                    </Grid>
                                </Paper>    
                            </Grid>
                        </Grid>
                    </Box>
                </Grid>
            </Grid>
            
        </>
    )
}
